"use client";

import { useState } from "react";

export default function StudentDashboard() {
  const [showEmailModal, setShowEmailModal] = useState(false);
  const [profEmail, setProfEmail] = useState("");

  return (
    <div className="bg-gray-50 min-h-screen flex flex-col">
      {/* Top Navigation Bar */}
      <nav className="bg-gradient-to-r from-blue-600 to-indigo-600 text-white p-4 shadow-md flex justify-between items-center">
        <h1 className="text-xl font-bold">ProfConnect</h1>

        <div className="flex gap-3">
          <button
            onClick={() => (window.location.href = "/")}
            className="bg-white text-indigo-700 px-4 py-2 rounded-lg font-medium hover:bg-gray-100 transition"
          >
            Home
          </button>

          <button
            onClick={() => {
              sessionStorage.removeItem("token");
              window.location.href = "/";
            }}
            className="bg-white text-indigo-700 px-4 py-2 rounded-lg font-medium hover:bg-gray-100 transition"
          >
            Logout
          </button>
        </div>
      </nav>

      {/* Header */}
      <header className="bg-gradient-to-r from-blue-600 to-indigo-600 text-white p-5 shadow-md flex justify-between items-center">
        <h1 className="text-2xl font-bold">ProfConnect - Student Dashboard</h1>
      </header>

      {/* Course Recommendation */}
      <section className="p-6">
        <a
          href="#"
          className="bg-purple-600 hover:bg-purple-700 text-white font-semibold px-6 py-3 rounded-2xl shadow-md transition inline-block"
        >
          Course Recommendation System
        </a>
      </section>

      {/* Filter */}
      <section className="bg-white shadow-md p-6 sticky top-0 z-10 flex items-center gap-4">
        <label className="font-medium text-gray-700">Filter:</label>
        <select className="border rounded-lg px-3 py-2 focus:ring focus:ring-indigo-200">
          <option value="">Select</option>
          <option>Institute Name</option>
          <option>Recently Added</option>
        </select>
      </section>

      {/* Opportunities */}
      <main className="p-6 grid gap-6 md:grid-cols-2 lg:grid-cols-3">
        {/* Replace with dynamic API data later */}
        <div className="bg-white rounded-2xl shadow-md p-6 hover:shadow-lg transition flex flex-col">
          <h2 className="text-xl font-bold text-green-700 mb-2">Internship</h2>

          <p className="text-gray-600 mb-1">
            <strong>Professor:</strong> Dr. A
          </p>
          <p className="text-gray-600 mb-1">
            <strong>Institute:</strong> BITS Pilani
          </p>
          <p className="text-gray-600 mb-1">
            <strong>Field:</strong> Data Structures
          </p>
          <p className="text-gray-600 mb-1">
            <strong>Duration:</strong> 6 months
          </p>
          <p className="text-gray-600 mb-3">
            <strong>Pre-requisites:</strong> C++, Algorithms
          </p>

          <a href="#" className="text-indigo-600 hover:underline text-sm mb-3">
            View PDF Details
          </a>

          <button
            onClick={() =>
              window.open(
                `https://mail.google.com/mail/?view=cm&fs=1&to=profA@bits-pilani.ac.in&su=Application for Opportunity&body=Dear Professor,%0D%0A%0D%0AI am interested in applying for this opportunity.%0D%0A%0D%0ARegards,`,
                "_blank"
              )
            }
            className="mt-auto bg-green-600 hover:bg-green-700 text-white rounded-lg px-4 py-2 transition"
          >
            Apply
          </button>
        </div>
      </main>

      {/* EMAIL POPUP MODAL */}
      {showEmailModal && (
        <div className="fixed inset-0 bg-black/30 backdrop-blur-sm flex justify-center items-center z-50">
          <div className="bg-white rounded-2xl shadow-xl p-6 w-full max-w-md relative">
            {/* Close Button */}
            <button
              onClick={() => setShowEmailModal(false)}
              className="absolute top-3 right-3 text-gray-500 hover:text-gray-700"
            >
              âœ–
            </button>

            <h2 className="text-xl font-bold mb-4 text-indigo-700">
              Send Email
            </h2>

            <label className="block font-medium text-gray-700">To:</label>
            <input
              type="email"
              value={profEmail}
              readOnly
              className="w-full border rounded-lg px-3 py-2 mb-4 bg-gray-100"
            />

            <label className="block font-medium text-gray-700">Subject</label>
            <input
              type="text"
              placeholder="Write subject..."
              className="w-full border rounded-lg px-3 py-2 mb-4"
            />

            <label className="block font-medium text-gray-700">Message</label>
            <textarea
              rows={4}
              placeholder="Write your message..."
              className="w-full border rounded-lg px-3 py-2 mb-4"
            ></textarea>

            <button
              className="w-full bg-indigo-600 hover:bg-indigo-700 text-white py-2 rounded-lg transition"
              onClick={() => {
                alert("Email Sent Successfully!");
                setShowEmailModal(false);
              }}
            >
              Send Email
            </button>
          </div>
        </div>
      )}
    </div>
  );
}
